import React from 'react';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import Header from './../Components/BaseComponents/Header';
import ReactWeb from './../Components/BusinessComponents/ReactWeb';
import ReactNative from './../Components/BusinessComponents/ReactNative';
import ReactBlogs from './../Components/BusinessComponents/ReactBlogs';
import ReactTools from './../Components/BusinessComponents/ReactTools';
import Feedback from './../Components/BusinessComponents/Feedback';
import SubmitRequest from './../Components/BusinessComponents/SubmitRequest';

import './App.css';


const App = () => (
  <Router>
    <div className="App">
      <Header />
      <Route exact path="/beta/" component={ReactWeb} />
      <Route path="/beta/ReactWeb" component={ReactWeb} />
      <Route path="/beta/ReactBlogs" component={ReactBlogs} />
      <Route path="/beta/ReactNative" component={ReactNative} />
      <Route path="/beta/ReactTools" component={ReactTools} />
      <Route path="/beta/Feedback" component={Feedback} />
      <Route path="/beta/SubmitRequest" component={SubmitRequest} />
    </div>
  </Router>
)

export default App;


